
import { defineComponent, ref, computed } from 'vue';
import moment from 'moment'
import { useStore } from '@/store'
import TimelinePost from '@/components/TimelinePost.vue'
import { Post } from '../mocks'

type Period = 'Today' | 'This Week' | 'This Month'





export default defineComponent({
  name: 'Timeline',

  components: {
    TimelinePost,
  },

  async setup() {
    const periods : Period[] = ['Today', 'This Week', 'This Month']
    const currentPeriod = ref<Period>('Today')
    const store = useStore()

    if(!store.getState().posts.loaded) {
      await store.fetchPosts()
    }

    const allPosts: Post[] = store.getState().posts.ids.reduce<Post[]>((accum : any, id : string) => {
        const thePost = store.getState().posts.all.get(id)
        if(!thePost) {
          return Error('The post was not found')
        }
        return accum.concat(thePost)
    }, [])
    const posts = computed(() => {
      return allPosts.filter(post => {
        if(currentPeriod.value === 'Today') {
          return post.created.isAfter(moment().subtract(1, 'day'))
        }
        if(currentPeriod.value === 'This Week') {
          return post.created.isAfter(moment().subtract(1, 'week'))
        }
        if(currentPeriod.value === 'This Month') {
          return post.created.isAfter(moment().subtract(1, 'month'))
        }

        return false
      })
    })

    const setPeriod = (period: Period) => {
      currentPeriod.value = period
    }

    return {
      posts,
      periods,
      currentPeriod,
      setPeriod
    }
  }
});
